@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SF_HambakSnow';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'NanumSquareNeo-Variable';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/NanumSquareNeo-Variable.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

body {
  /* font-family: "GangwonEdu_OTFBoldA"; */
  font-family: "NanumSquareNeo-Variable";
  background-color: #F1F6F9;
  line-height: 1.5;
  /* font-weight: bolder; */
}

header {
  font-family: "SF_HambakSnow";
  font-size: 120px;
  padding:150px;
  margin-bottom: 100px;
  /* margin-top: 170px;
  margin-left: 100px;
  margin-right: 100px;
  margin-bottom: 100px; */
  line-height: 0.8;
  background: linear-gradient(to left, #b366ff, #001b44);
}

#headP {
  font-size: 45px;
  color: rgb(78, 74, 181);
}

#headP2 {
  font-size: 60px;
  color: #7d7bd6;
}
#headP3 {
  margin-top: 0px;
  margin-bottom:50px;
  color: #ccdbee;
  
}
.navContainer {
  font-family: "SF_HambakSnow";
  width: 450px;
  font-size: 40px;
  background-color: #9da7bc;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  display: flex;
  /* navbar를 반투명하게 */
  opacity: 0.8;
  backdrop-filter: blur(30px);
}

.category {
  margin: 0px;
  margin-left: 15px;
  color: white;
  padding: 10px 20px;
  margin-right: 80px;
}

#banner {
  max-width: 1800px;
  max-height: auto;
}

main:not(#banner) {
  /* font-family: "GangwonEdu_OTFBoldA"; */
  font-family: "NanumSquareNeo-Variable";
  margin-left: 20px;
  background-color: #F1F6F9;
  font-size: 70px;
}

footer {
  font-family: "NanumSquareNeo-Variable";
  padding: 80px;
  padding-left: 150px;
  background-color: #737b88;
  color: white;
  font-size: 1.7em;
  margin-top: 500px;
  line-height: 2.5em;
}
.footcon img{
  width: 80px;
}
#foottitle{
  font-size: 1.9em;
  font-weight: bolder;
}

a:link:not(.headL, .category, .footlink),
a:visited {
  text-align: center;
  background-color: #9da7bc;
  color: #212A3E;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  width: 250px;
}

.headL {
  text-decoration: none;
}

a:hover:not(.headL, .category, .footlink, .cateLink),
a:active:not(.headL, .category, .footlink, .cateLink) {
  background-color: #212A3E;
  color: white;
}


button {
  font-family: "NanumSquareNeo-Variable";
  font-size: 0.7em;
  font-weight: bolder;
  width: 500px;
  height: 70px;
  background-color: white;
  margin-bottom: 100px;
  text-align: center;
  color: #212A3E;
  /* border: 1px #3399dd solid; */
  border: 0px;
  cursor: pointer;
  transition: all 0.9s, color 0.3;
}

button:hover {
  color: rgb(112, 140, 233);
}
